@import 'tools/_normalize';
@import 'tools/_fonts';


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1084px;
  padding: 0 30px;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;

  height: 80px;
  padding: 0 30px;

  .logo {
    height: auto;
    width: 133px;
  }
  .nav {
    display: flex;
    flex-flow: row nowrap;
    height: 100%;

    &-item {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      height: 100%;
      margin-right: 40px;
      color: #000000;
      cursor: pointer;
      &:last-child {
        margin: 0;
      }
    }
  }
}

.hero {
  color: #FFFFFF;
  background: url('../images/background.jpg') no-repeat center / cover;

  .container{
    position: relative;
    height: calc(100vh - 80px);
    min-height: 610px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
  }
}

.hero_title {
  text-align: center;
  font-family: Arial, sans-serif;
  font-weight: bold;
  font-size: 32px;
  color: #FFFFFF;
  margin-bottom: 32px;
}

.vacancy_title {
  font-family: 'Helvetica Neue';
  margin-top: -30px;
  margin-bottom: 20px;
  text-align: center;
}

.join_button {
  font-family: 'Helvetica Neue';
  color: #FFFFFF;
  border: 2px solid #FFCC33;
  padding: 12px 30px;
  text-align: center;
}

.arrow_down {
  position: absolute;
  transform: translate(-50%, -50%);
  bottom: 30px;
  left: 50%;
}

.description {
  font-family: 'Helvetica Neue';
  text-align: center;
  padding: 60px 0;
  font-size: 22px;
}

.separator {
  height: 2px;
  width: 78px;
  background: #FFCC33;
  margin: 0 auto;
  margin-top: 24px;
}

.conditions {
  background: url('../images/conditions_background.jpg') no-repeat center / cover;
  color: #FFFFFF;
  padding: 60px 0px;

  .separator {
    margin: 0;
    margin-bottom: 16px;
  }
}

.conditions_desctiprion {
  font-family: Arial, sans-serif;
  line-height: 22px;
}

.conditions_wrapper {
  margin-bottom: 32px;
  
  &:last-child {
    margin: 0;
  }
}

.title {
  font-family: 'Helvetica Neue';
  font-size: 24px;
  margin-bottom: 16px;
}

.conditions_description {
  line-height: 22px;
}

.terms {
  padding: 60px 0;
  background: #F4F4F4;
  .container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  
  span {
    font-weight: bold;
  }
}

.term_item {
  font-family: Arial, sans-serif;
  line-height: 22px;
  display: flex;
  flex-flow: row nowrap;
  line-height: 22px;
  margin-bottom: 16px;

  &:last-child {
    margin: 0;
  }
}


.circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #FFCC33;
  margin-right: 16px;
  margin-top: 5px;
}

.big_line {
  width: 10px;
  background: #FFCC33;
}

.way {
  padding: 60px 0;

  .container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }

  .wrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
  }

  .title {
    width: 100%;
    text-align: center;
    margin-bottom: 42px;
  }
  
  .join_button {
    margin: 0 auto;
    margin-top: 42px;
    color: #000;
  }
}

.way_item {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  text-align: center;
  width: 30%;
  min-width: 280px;

  &:last-child {
    margin: 0;
  }
}

.way_img {
  margin-bottom: 9px;
  height: 40px;
  width: auto;
}

.way_title {
  font-family: 'Helvetica Neue';
  font-weight: bold;
  margin-bottom: 9px;
}

.join {
  padding: 60px 0;
  background: url('../images/comand_background.jpg') no-repeat center / cover;
  .container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }
  .title {
    color: #FFFFFF;
    text-align: center;
    margin: 0;
  }

  .join_button {
    margin: 0 auto;
  }
}

.footer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-end;
  background: #000;
  color: #FFFFFF;
  font-family: 'Helvetica Neue';
  padding: 30px 30px;
}
.copyright {
  flex: 0 0 50%;
}

.social {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: flex-end;
  flex: 0 0 50%;

  .icons {
    img {
      height: 17px;
      width: auto;
      // margin-right: 22px;
    }
  }

  &_icon {
    margin-right: 22px;

    &:last-child {
      margin: 0;
    }
    
  }
} 
.social_title {
  margin-bottom: 32px;
}

@media only screen and (max-width: 900px) {
  .way {
    &_item{
      margin-bottom: 25px;
    }


    .wrapper {
      justify-content: center;
    }
  }


}

@media only screen and (max-width: 415px) {
  .header {
    justify-content: center;
    .nav {
      display: none;
    }
  }
  
  .join_button {
    width: 100%;
  }

  .hero_title {
    font-size: 24px;
  }

  .social_title {
    text-align: right;
  }

  .join_button {
    width: 100%;
  }

  .big_line {
    display: none;
  }
}